// configure 변수를 전역으로 선언
const React = require('react') as typeof import('react');
const ReactNative = require('react-native') as typeof import('react-native');
const { NativeModules, TextInput, TouchableOpacity, Text: RNText, View } = ReactNative;
require('react-native-gesture-handler/jestSetup');

// testing-library 관련 설정
let configure: any;
try {
  const rtl = require('@testing-library/react-native');
  configure = rtl.configure;
  require('@testing-library/jest-native/extend-expect');
  
  if (configure) {
    configure({
      asyncUtilTimeout: 10000,
    });
  }
} catch (error) {
  console.warn('Testing library not loaded:', error);
}

// FireEvent 관련 오류(Cannot read properties of undefined (reading 'S')) 해결을 위한 설정
jest.mock('@testing-library/react-native', () => {
  const actual = jest.requireActual('@testing-library/react-native');
  
  // fireEvent 메서드 오버라이드
  const fireEvent = {
    ...actual.fireEvent,
    press: (element) => {
      if (element && element.props && element.props.onPress) {
        element.props.onPress();
        return true;
      }
      console.warn('Failed to press element - no onPress handler found');
      return false;
    },
    changeText: (element, text) => {
      if (element && element.props && element.props.onChangeText) {
        element.props.onChangeText(text);
        return true;
      }
      console.warn('Failed to change text - no onChangeText handler found');
      return false;
    }
  };
  
  return {
    ...actual,
    fireEvent,
  };
});
// Reanimated 모킹
jest.mock('react-native-reanimated', () => {
  const Reanimated = require('react-native-reanimated/mock');
  Reanimated.default.call = () => {};
  return Reanimated;
});

// Alert 모킹을 별도로 분리
jest.mock('react-native/Libraries/Alert/Alert', () => ({
  alert: jest.fn(),
}));

jest.mock('react-native', () => {
  const reactNative = jest.requireActual('react-native');
  reactNative.NativeModules.NativeAnimatedHelper = {
    startAnimatingNode: jest.fn(),
    stopAnimation: jest.fn(),
  };
  
  return reactNative;
});
// AsyncStorage 모킹
jest.mock('@react-native-async-storage/async-storage', () => ({
  setItem: jest.fn(() => Promise.resolve()),
  getItem: jest.fn(() => Promise.resolve(null)),
  removeItem: jest.fn(() => Promise.resolve()),
}));

// SafeAreaContext 모킹
jest.mock('react-native-safe-area-context', () => ({
  SafeAreaProvider: function SafeAreaProvider({ children }: { children: React.ReactNode }) { 
    return children; 
  },
  SafeAreaView: function SafeAreaView({ children }: { children: React.ReactNode }) { 
    return children; 
  },
  useSafeAreaInsets: () => ({ top: 0, right: 0, bottom: 0, left: 0 }),
}));

// Navigation 모킹
jest.mock('@react-navigation/native', () => ({
  ...jest.requireActual('@react-navigation/native'),
  useNavigation: () => ({
    navigate: jest.fn(),
    goBack: jest.fn(),
  }),
}));

// React Native Vector Icons 모킹
jest.mock('react-native-vector-icons/MaterialCommunityIcons', () => ({
  __esModule: true,
  default: 'MockedMaterialCommunityIcons',
}));

// React Native Paper 모킹 개선
jest.mock('react-native-paper', () => {
  const React = require('react');
  const RN = require('react-native');
  
  return {
    TextInput: function MockTextInput(props: any) {
      const { label, testID, onChangeText, value, ...restProps } = props;
      return React.createElement(RN.TextInput, {
        testID: testID || 'text-input',
        onChangeText: onChangeText,
        value: value,
        placeholder: label || props.placeholder,
        accessibilityLabel: label,
        editable: props.editable !== false,
        accessible: true,
        ...restProps
      });
    },
    Button: function MockButton(props: any) {
      return React.createElement(RN.TouchableOpacity, 
        { 
          testID: props.testID,
          onPress: props.onPress,
          accessible: true,
          accessibilityRole: 'button',
          ...props 
        },
        React.createElement(RN.Text, null, props.children)
      );
    },
    Chip: function MockChip(props: any) {
      return React.createElement(RN.TouchableOpacity, 
        { 
          testID: props.testID,
          onPress: props.onPress,
          style: [{ borderRadius: 20, padding: 8, margin: 4 }, props.style],
          ...props 
        },
        React.createElement(RN.Text, null, props.children)
      );
    },
    Text: function MockText(props: any) {
      return React.createElement(RN.Text, props);
    },
    ActivityIndicator: function MockActivityIndicator(props: any) {
      return React.createElement(RN.ActivityIndicator, props);
    },
    Provider: function MockProvider({ children }: { children: any }) {
      return children;
    },
  };
});
// 플랫폼 관련 모킹
NativeModules.RNCNetInfo = {
  getCurrentState: jest.fn(() => Promise.resolve()),
  addListener: jest.fn(),
  removeListeners: jest.fn(),
};



// 콘솔 오류 메시지 필터링 - act 경고와 특정 오류 억제
// jest.setup.ts의 console.error 부분 수정
const originalConsoleError = console.error;
console.error = (...args) => {
  if (
    typeof args[0] === 'string' &&
    (args[0].includes('act(') || 
     args[0].includes('inside a test was not wrapped in act') ||
     args[0].includes('unmounted component') ||
     args[0].includes('감정 로드 오류') ||
     args[0].includes('챌린지 데이터 로딩 오류') ||
     args[0].includes('Error: API 오류'))
  ) {
    return; // 특정 오류 메시지 무시
  }
  originalConsoleError(...args);
};